export const initialState = {
    term: null,
  };
  

export const actionTypes = {
    SET_SEARCH_TERM:'SET_SEARCH_TERM',
};

 const reducer = (state , action) => {    //The job of reducer is to listen the dispatched Items
    console.log(action)
    switch (action.type){
        case actionTypes.SET_SEARCH_TERM:
            return{
                ...state,
                term: action.term,
            }
            default:
                return state;
    }
};

export default reducer;